<template>
  <div class="mapbox">
    <div class="info-describe">
      <div class="content">
        <h1>FIND MY COMPANY</h1>
        <div class="content-full">
          <p>通过对路径的最优分析，快速准确的制定导航路线</p>
          <p>仿真动画模拟，方便快捷寻找到目标位置<a class="reload-map" v-on:click=btnClick()>重新加载</a></p> 
          
        </div>
       
        <div class="datail_btn">查看演示</div>
      </div>
    </div>
    <div class="map-box">
      <div ref="basicMapbox" :style="mapSize">
        <div id="mark"></div>
      </div>
    </div>
  </div>
</template>
<script>
import mapboxgl from "mapbox-gl";
import axios from "axios";
import cheapRuler from "cheap-ruler";
export default {
  props: {
    mapWidth: {
      type: String
    },
    mapHeight: {
      type: String
    }
  },
  data() {
    return {
      map: {},
      startPoint: [104.0314636002422, 30.619105082922786],
      endPoint: [104.03696970314019, 30.623525498430325],
      navigationData: {},
      pause: true,
      ruler: cheapRuler(30.6, "meters"),
      lastQueryTime: null,
      truckLocation: [104.0314636002422, 30.619105082922786],
      requestAnimation: null,
      truckMarker: null
    };
  },
  mounted() {
    this.initMap();
    this.getNavigationData();
    this.creatMarkPoint();
    this.eventLister();
  },
  methods: {
    // 初始化
    initMap() {
      mapboxgl.accessToken =
        "pk.eyJ1IjoibGluZ29uIiwiYSI6ImNqaTJoeGcxbzF0c2Yzb29iZmt3cnJ6NncifQ.RlC5XxaX4cHyhxIhquZQPA";
      this.map = new mapboxgl.Map({
        container: this.$refs.basicMapbox,
        style: "mapbox://styles/lingon/cjj6wneqq01pj2qpi5ypd2wte",
        center: [104.0314636002422, 30.619105082922786],
        zoom: 16,
        pitch: 75,
        bearing: 120
        // scrollZoom: false
      });
    },
    loadLayer(geometryData) {
      //添加线
      this.map.addLayer({
        id: "route",
        type: "line",
        source: {
          type: "geojson",
          data: {
            type: "Feature",
            properties: {},
            geometry: geometryData
          }
        },
        layout: {
          "line-join": "round",
          "line-cap": "round"
        },
        paint: {
          "line-color": "#f00",
          "line-width": 8
        }
      });
    },
    getNavigationData() {
      //设置请求路径
      const point = [this.startPoint, this.endPoint];
      const url =
        "https://api.mapbox.com/directions/v5/mapbox/walking/" +
        point.join(";") +
        ".json?access_token=" +
        mapboxgl.accessToken +
        "&steps=true&alternatives=true&overview=full&geometries=geojson";
      const _this = this;
      axios.get(url).then(function(result) {
        if (result.statusText == "OK") {
          _this.navigationData = result.data.routes[0];
          _this.loadLayer(result.data.routes[0].geometry);
          // _this.lastQueryTime = Date.now();
          // _this.tick();
        }
      });
    },
    btnClick() {
      if (this.requestAnimation) {
        cancelAnimationFrame(this.requestAnimation);
        this.pause = true;
      }
      this.lastQueryTime = Date.now();
      this.tick();
    },
    tick() {
      if (this.pause) {
        this.moveTruck();
        this.requestAnimation = requestAnimationFrame(this.tick);
      }
    },
    moveTruck() {
      var timeElapsed = (Date.now() - this.lastQueryTime) / 20;
      var timeRatio = timeElapsed / this.navigationData.duration;
      var progressDistance = this.navigationData.distance * timeRatio;
      var newSpot = this.ruler.along(
        this.navigationData.geometry.coordinates,
        progressDistance
      );

      var bearing = this.ruler.bearing(this.truckLocation, newSpot);
      this.truckLocation = newSpot;
      if (isNaN(newSpot[0]) || isNaN(newSpot[1])) {
        return;
      }
      this.truckMarker.setLngLat(this.truckLocation);

      this.map.setCenter(newSpot).easeTo({ bearing: bearing, duration: 100 });
      //this.map.setCenter(newSpot);
      //this.map.setBearing(bearing);
      this.map.easeTo({ bearing: bearing, duration: 50 });
      if (
        this.navigationData &&
        this.ruler.distance(
          this.truckLocation,
          this.navigationData.geometry.coordinates[
            this.navigationData.geometry.coordinates.length - 1
          ]
        ) < 1
      ) {
        this.pause = false;
      }
    },
    creatMarkPoint() {
      var marker = this.$el.querySelector("#mark");
      marker.classList = "truck";
      this.truckMarker = new mapboxgl.Marker({
        element: marker,
        anchor: "center"
      })
        .setLngLat(this.startPoint)
        .addTo(this.map);
    },
    eventLister() {
      this.$refs.basicMapbox.children[1].addEventListener("wheel", function(e) {
        e.stopPropagation(); //终止事件冒泡
      });
    }
  },
  computed: {
    mapSize() {
      let styleObj = {
        width: this.mapWidth,
        height: this.mapHeight
      };
      return styleObj;
    }
  }
};
</script>
<style>
@import url("mapbox-gl/dist/mapbox-gl.css");
.mapbox {
  height: 100%;
  width: 100%;
  text-align: left;
}
.btn {
  width: 50px;
  height: 20px;
  position: absolute;
  left: 20%;
  top: 20%;
  z-index: 2;
  background-color: black;
  color: white;
  opacity: 0.2;
}

.truck {
  width: 20px;
  height: 20px;
  border: 2px solid #fff;
  border-radius: 50%;
  background: #3887be;
  pointer-events: none;
}
.info-describe {
  float: left;
  width: 40%;
  height: 100%;
  background: #424d5c;
}
.map-box {
  width: 60%;
  height: 100%;
  float: left;
  box-shadow: -5px 0px 5px #55667d;
}

.info-describe > .content > h1 {
  font-size: 2.5rem;
  color: #ffffff;
  text-align: center;
  width: 50%;
  margin: 0 25%;
}
.content {
  position: relative;
  top: 20%;
  text-align: center;
}
.content-full p {
  font-size: 1.125rem;
  color: #ffffff;
  text-align: left;
  margin: 0 10%;
}
.reload-map {
  cursor: pointer;
  color: #000;
  font-size: 12px;
}
.datail_btn {
  width: 50%;

  border: 1px solid #ffffff;
  text-align: center;
  cursor: pointer;
  line-height: 60px;
  margin: 8% 25%;
  color: #ffffff;
}
@media all and (max-width: 600px) {
  .info-describe {
    float: left;
    width: 100%;
    height: 60%;
    background: #424d5c;
  }
  .map-box {
    width: 100%;
    height: 40%;
    float: left;
    box-shadow: -5px 0px 5px #55667d;
  }
}
</style>


