import Vue from 'vue'
import Router from 'vue-router'
import content from '@/components/content/content'
import aboutUs from '@/components/aboutUs'
import contactUs from '@/components/contactUs'
import teamIntro from '@/components/teamIntro'
import product from '@/components/product'

Vue.use(Router)

export default new Router({
  mode: "history",
  routes: [
    {
      path: '/',
      redirect: '/content'  // 重定向默认就跳转此页面
    },
    {
      path: '/content',
      name: 'content',
      component: content
    },
    {
      path: '/aboutUs',
      name: 'aboutUs',
      component: aboutUs
    },
    {
      path: '/contactUs',
      name: 'contactUs',
      component: contactUs
    },
    {
      path: '/product',
      name: 'product',
      component: product
    },
    {
      path: '/teamIntro',
      name: 'teamIntro',
      component: teamIntro
    }
  ]
})



