// 人口流动信息
<template>
	<div class="swiper-container" id="pepolemove">
		<div class="swiper-wrapper ">
			<div class="swiper-slide" id="pepolemoveMap">

			</div>
		</div>
		<div class="pepolemoveBar"></div>
	</div>
</template>
<script>
	import Swiper from 'swiper';
	import echarts from 'echarts';
	import 'echarts/extension/bmap/bmap';
	import 'echarts/map/js/china.js';
	import {MP} from '../public/map.js';
	import {loadMap} from './pepolemoveMap.js'
	export default {
		name: "pepolemove",
		data() {
			return {
				title: "pepolemove",
				ak:	"UgfruY4Zph4g4rfRROEymxzWnU27zCsy"
			}
		},
		mounted() {
			this.$nextTick(function(){  
            	var _this = this;
            	console.log(MP(_this.ak))
            	MP(_this.ak).then(BMap => { 
            		console.log(111)
            		var myCharts = echarts.init(document.getElementById("pepolemoveMap"))
            		loadMap("loadData",myCharts);
              		
            	},function(error){
            		console.log(222)
            	})
            })
		   
		},
		methods:{
			
		},
	};
	
</script>

<style>
	#pepolemove {
		width: 100%;
		height: 100%;
		position: relative;
	}
	.pepolemoveBar{
		width: 350px;
		height: 600px;
		position: absolute;
		top: 150px;
		right: 150px;
		background: red;
		z-index: 11;
	}
</style>


