<template>
	<div class="swiper-container" id="indexSwiper">
		<div class="swiper-wrapper">
			<div class="swiper-slide">
				<pepoleMove></pepoleMove>
			</div>
			<div class="swiper-slide">
				<div style="width: 100%;height: 100%;">
					<airquality></airquality>
				</div>
				
			</div>
			<div class="swiper-slide swiper-no-swiping">
				<mapbox-map mapWidth="100%" mapHeight="100%"></mapbox-map>
			</div>
		</div>
		<!-- 如果需要分页器 -->
		<div class="swiper-pagination"></div>
		<!-- 如果需要滚动条 -->
		<!--<div class="swiper-scrollbar"></div>-->
	</div>
</template>

<script>
	import Swiper from 'swiper';
	import pepoleMove from './pepoleMove';
	import gisMap from '@/components/gis/mapbox';
	import airquality from './airquality';
	export default {
		name: 'IndexSwiper',
		components: {
			'pepoleMove': pepoleMove,
			'mapboxMap':gisMap,
			'airquality':airquality
		},
		data() {
			return {
				msg: 'Welcome to Your Vue.js App'
			}
		},
		mounted() {
			new Swiper('#indexSwiper', {
				direction: 'vertical',
				//鼠标滚动
				mousewheel: true,
				//分页器
				pagination: {
					el: '.swiper-pagination',
					//type: 'custom',
					clickable: true, //分页器的小点可点
					renderBullet: function(index, className) {
						return '<span class="' + className + '"></span>';
					},
				},
			})
		}
	}
</script>

<style scoped="scoped">

	#indexSwiper {
		height: 100%;
		width: 100%;
	}
	
	p {
		text-align: center;
		color: #fff;
		line-height: 600px;
		font-size: 36px;
		height: 100%;
	}
	
	.p1 {
		background: pink;
	}
	
	.p2 {
		background: #660066;
	}
	
	.p3 {
		background: #C21F39;
	}
</style>


