<template>
	<div id="app">
		<div class="header-box">
			<componentsHeader></componentsHeader>
		</div>
		
		<!-- <IndexSwiper></IndexSwiper> -->
		<!-- <transition> -->
				<router-view></router-view>
		<!-- </transition> -->

	</div>
</template>

<script>
  	import componentsHeader from './components/header/header';  
	//import IndexSwiper from './components/content/content';
	
	export default {
		name: 'App',
		components: {
			'componentsHeader':componentsHeader
			//'IndexSwiper':IndexSwiper
		},
		created(){
			this.$router.push('/content');  // 页面加载时跳转
		},
		data() {
			return {
				msg1: '首页的头部',
				msg2: '首页的内容部分'
			}
		}
	}
</script>

<style scoped="scoped">
#app {
  width: 100%;
  height: 100%;
  background: #fafafa;
}

.header-box {
  width: 100%;
  height: 60px;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 99;
}

.count {
  width: 100%;
  height: 50%;
  background: yellow;
}

.footer {
  width: 100%;
  height: 20%;
  background: greenyellow;
}
.enter-active, .leave-active {
  transition: opacity .3s ease;
}
.enter, .fade-leave-to
/* .component-fade-leave-active for below version 2.1.8 */ {
  opacity: 0;
}
</style>



