<template>
	<div class="productBox containerBox" id="productBox">
		<div class="swiper-wrapper">
			<div class="swiper-slide">
				<pepolemoveModel></pepolemoveModel>
			</div>
			<div class="swiper-slide">
				<airqualityModel></airqualityModel>
			</div>
			
		</div>
		<!-- 如果需要分页器 -->
		<div class="swiper-pagination"></div>
	</div>
	
</template>

<script>
	import Swiper from 'swiper';
	import airquality from './productmodel/airqualityDatail';
	import pepolemove from './productmodel/pepolemoveDatail';
	
	export default {
		name: 'product',
		components:{
			"airqualityModel":airquality,
			"pepolemoveModel":pepolemove
		},
		data() {
			return {
				title: "产品技术"
			}
		},
		mounted (){
			new Swiper('#productBox', {
				direction: 'vertical',
				//鼠标滚动
				mousewheel: true,
				//分页器
				pagination: {
					el: '.swiper-pagination',
					//type: 'custom',
					clickable: true, //分页器的小点可点
					renderBullet: function(index, className) {
						return '<span class="' + className + '"></span>';
					},
				},
			})
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.containerBox {
		overflow: hidden;
		background: #fafafa;
		width: 100%;
		height: 100%;
	}
	
	.containerBox>.swiper-wrapper {
		width: 100%;
		height: 100%;
	}
</style>


