<template>
	<div class="airquality_box">
		<div class="text_btn_box">
			<h1>空气质量分析</h1>
			<div>
				<span>空气质量分析空气质量分析空气质量分析空气质量分析空气质量分析空气质量分析</span>
			</div>
			<div class="datail_btn">查看演示</div>
		</div>

		<div class="scatter_box" id="scatter_box">

		</div>
	</div>
</template>

<script>
	import echarts from 'echarts';
	import axios from 'axios'
	export default {
		name: "airquality",
		data() {
			return {
				loadeechart:"Welcome",
				pmLower: [],
				pmMiddle: [],
				aqiData: [],
				dayData: []
			}
		},
		created() {
			
		},
		mounted() {
			this.loadChartData()
		},
		 beforeDestroy() {
           
        },
        destroyed() {
         
        },
		methods: {
			loadChartData(){
				axios.get("../../static/dataJson/airQuality.json").then((res) => {
					if(res.status == 200){
						//console.log(res)
						let resData = res.data.result;
						resData.forEach((params) => {
							//console.log(this)
							this.pmLower.push(params.pmLower);
							this.pmMiddle.push(params.pmMiddle);
							this.aqiData.push(params.aqiData);
							this.dayData.push(params.day);
							
						});
						this.initChart();
					}
				})
				.catch();
			},
			initChart() {
				var myChart = this.$echarts.init(document.getElementById("scatter_box"));
				var schema = [{
						name: 'date',
						index: 0,
					},
					{
						name: 'PM2.5',
						index: 1,
						text: 'PM2.5'
					},
					{
						name: 'AQIindex',
						index: 2,
						text: 'AQI指数'
					},
					{
						name: 'PM10',
						index: 3,
						text: 'PM10'
					},

				];

				var itemStyle = {
					normal: {
						opacity: 0.8,
						shadowBlur: 10,
						shadowOffsetX: 0,
						shadowOffsetY: 0,
						shadowColor: 'rgba(0, 0, 0, 0.5)'
					}
				};
				var option = {
					backgroundColor:'',
					color: [
						'#dd4444'
					],
					legend: {
						y: 'top',
						data: ['北京'],
						textStyle: {
							color: '#fff',
							fontSize: 16
						}
					},
					 dataZoom: [{
						type: 'slider',	
						show:true,
						realtime:true,
						height: 12,
						start:4,
						end:96,
						bottom:0,
						backgroundColor:"#04273b",
						handleIcon: 'M10.7,11.9v-1.3H9.3v1.3c-4.9,0.3-8.8,4.4-8.8,9.4c0,5,3.9,9.1,8.8,9.4v1.3h1.3v-1.3c4.9-0.3,8.8-4.4,8.8-9.4C19.5,16.3,15.6,12.2,10.7,11.9z M13.3,24.4H6.7V23h6.6V24.4z M13.3,19.6H6.7v-1.4h6.6V19.6z',
						handleSize: '100%',
						handleStyle: {
							color: '#66c9a4',
							shadowBlur: 3,
							shadowColor: 'rgba(0, 0, 0, 0.7)',
							shadowOffsetX: 3,
							shadowOffsetY: 3
						},
						fillerColor:'#66c9a4',
						showDataShadow: false,
						zoomLock: true
					},
					{
						type:'inside',
						realtime:true,
						start:0,
						end:100,
						zoomOnMouseWheel:true
					}],
					grid: {
						x: '10%',
						x2: 150,
						y: '18%',
						y2: '10%'
					},
					tooltip: {
						padding: 10,
						backgroundColor: '#222',
						borderColor: '#777',
						borderWidth: 1,

						formatter: function(obj){

							var value = obj.value;
							var divBox = obj.name+'<br/>'+'<div><i style="display:inline-block;margin-right:5px;width:10px;height:10px;border-radius:6px;background:'+obj.color+'"></i>'+'<span>'+obj.seriesName+'：'+'</span>'+'<span>'+ value +'</span>' +'</div>';	
							return divBox;
						},
					},
					xAxis: {
						type: 'category',
						name: '日期',
						boundaryGap:true,
						data: this.dayData,
						nameGap: 16,
						nameTextStyle: {
							color: '#fff',
							fontSize: 14
						},
						//max: 31,
						splitLine: {
							show: false
						},
						axisLine: {
							lineStyle: {
								color: '#eee'
							}
						},
						axisTick: {
							alignWithLabel:true
						}
					},
					yAxis: {
						type: 'value',
						name: '数值',
						nameLocation: 'end',
						nameGap: 20,
						nameTextStyle: {
							color: '#fff',
							fontSize: 16
						},
						axisLine: {
							lineStyle: {
								color: '#eee'
							}
						},
						splitLine: {
							show: false
						}
					},
					visualMap: [ {
						left: 'right',
						right: 150,
                        top: '7%',
                        itemHeight: 80,
                        calculable: true,
                        text: ['圆形大小：AQI'],
                        textGap: 20,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            symbolSize: [10, 70]
                        },
                        outOfRange: {
                            symbolSize: [10, 70],
                            color: ['rgba(255,255,255,.2)']
                        },
                        controller: {
                            inRange: {
                                color: ['#dd4444']
                            },
                            outOfRange: {
                                color: ['#444']
                            }
                        }
                    },
                    {
                        left: 'right',
                        top: '38%',
                        right:'45%',
                        // dimension: 2,
                        // min: 0,
                        // max: 50,
                        itemHeight: 80,
                        calculable: true,
                       // precision: 0.1,
                        text: ['圆形大小：PM2.5'],
                        textGap: 20,
                        textStyle: {
                            color: '#fff'
                        },
                       inRange: {
                            symbolSize: [10, 70]
                        },
                        outOfRange: {
                            symbolSize: [10, 70],
                            color: ['rgba(255,255,255,.2)']
                        },
                        controller: {
                            inRange: {
                                color: ['#fec42c']
                            },
                            outOfRange: {
                                color: ['#444']
                            }
                        }
                    },
                     {
                        left: 'right',
                        bottom: '5%',
                        itemHeight: 80,
                        calculable: true,
                        text: ['圆形大小：PM10'],
                        textGap: 20,
                        textStyle: {
                            color: '#fff'
                        },
                        inRange: {
                            symbolSize: [10, 70]
                        },
                        outOfRange: {
                            symbolSize: [10, 70],
                            color: ['rgba(255,255,255,.2)']
                        },
                        controller: {
                            inRange: {
                                color: ['#80F1BE']
                            },
                            outOfRange: {
                                color: ['#444']
                            }
                        }
                    }],
					series: [
						{
							name: 'AQI',
							type: 'scatter',
							itemStyle: {
								color: '#dd4444'
							},
							data: this.aqiData
						},
						{
							name: 'PM2.5',
							type: 'scatter',
							itemStyle: {
								color: '#fec42c'
							},
							data: this.pmLower,
						},
						{
							name: 'PM10',
							type: 'scatter',
							itemStyle: {
								color: '#80F1BE'//#66c9a4
							},
							data: this.pmMiddle
						}
					]
				};
				myChart.setOption(option);
			}
		}
	}
</script>

<style scoped="scoped">
	.airquality_box {
		width: 100%;
		height: 100%;
		background: #044061;
		position: relative;
		
	}
	.datail_btn{
		width: 240px;
		height: 60px;
		border: 1px solid #FFFFFF;
		text-align: center;
		cursor: pointer;
		line-height:60px;
		margin-top: 100px;
	}
	.text_btn_box {
		width: 30%; 
		height: 70%;
		position: absolute; 
		bottom:60px;
		left: 100px;
		color: #ffffff;
		font-size:18px;
	}
	.text_btn_box>h1{
		font-size: 40px;
		color: #ffffff;
		text-align: center;
	}
	.scatter_box {
		width: 60%;
		height: 80%;
		position: absolute !important;
		bottom: 60px;
		right: 30px;
	}
</style>


