<template>
	<div class="containerBox">
		<div class="conBox">
			<div class="title"></div>
			<div class="moveBox">
				<div class="item">
					<div class="img"></div>
					<div class="foot"></div>
				</div>
				<div class="item">
					<div class="img"></div>
					<div class="foot"></div>
				</div>
				<div class="item">
					<div class="img"></div>
					<div class="foot"></div>
				</div>
				<div class="item">
					<div class="img"></div>
					<div class="foot"></div>
				</div>
				<div class="item">
					<div class="img"></div>
					<div class="foot"></div>
				</div>
			</div>
			<div class="teamBox">
				<div class="teamBoxBg">
					<div class="boxBtn  front_end"></div>
					<div class="boxBtn  back_end"></div>
					<div class="boxBtn  demand"></div>
					<div class="boxBtn  ui"></div>
					<div class="boxBtn  all"></div>
					<div class="boxBtn  manager"></div>
					<div class="boxBtn  gis"></div>
					<div class="boxBtn  data"></div>
					<div class="boxBtn  test"></div>
					<div class="boxBtn  operation"></div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name: 'team',
		data() {
			return {
				title: ""
			}
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.containerBox {
		background: #02124e;
		height: 100%;
		overflow: auto;
	}
	.moveBox{
		padding: 0 20px;
		height: 187px;
		overflow: hidden;
		margin-top: 20px;
	}
	.item{
		float: right;
		position: relative;
		width: 106px;
		margin: 0 10px;
	}
	.img{
		width: 106px;
		height: 106px;
		border-radius: 50%;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
		z-index: 2;
		background: #fff;
		background: url(../../static/img/chengdan.png) no-repeat center;
	}
	.foot{
		width: 106px;
    height: 187px;
		background: url(../../static/img/foot.gif) no-repeat bottom center;
		background-size: 168%;
	}

	.conBox {
		box-sizing: border-box;
		background: #02124e;
		padding-top: 180px;
		position: relative;
		width: 100%;
		height: 1080px;
	}

	.title {
		width: 100%;
		height: 101px;
		background: url(../../static/img/TEAMWORK.png) no-repeat center;
	}



	.teamBox {
		width: 100%;
		height: 521px;
		position: absolute;
		bottom: 0;
		left: 0;
		text-align: center;
	}

	.teamBoxBg {
		margin: auto;
		width: 1561px;
		height: 100%;
		position: relative;
		background: url(../../static/img/bg_team.png) no-repeat center;
	}

	.boxBtn {
		height: 44px;
		width: 82px;
		position: absolute;
		cursor: pointer;
	}

	.front_end {
		left: 368px;
		top: 243px;
		background: url(../../static/img/front_end.png) no-repeat center;
	}

	.back_end {
		left: 862px;
		top: 93px;
		background: url(../../static/img/back_end.png) no-repeat center;
	}
	.demand {
		left: 206px;
    top: 304px;
		background: url(../../static/img/demand.png) no-repeat center;
	}
	.ui {
		left: 422px;
    top: 344px;
		background: url(../../static/img/ui.png) no-repeat center;
	}
	.gis {
		left: 678px;
    top: 120px;
		background: url(../../static/img/gis.png) no-repeat center;
	}
	.all{
		left: 593px;
    bottom: 60px;
		background: url(../../static/img/all.png) no-repeat center;
	}
	.manager{
		left: 770px;
    top: 260px;
		background: url(../../static/img/manager.png) no-repeat center;
	}
	.data{
		left: 946px;
    top: 386px;
		background: url(../../static/img/data.png) no-repeat center;
	}
	.test{
		right: 350px;
    top: 205px;
		background: url(../../static/img/test.png) no-repeat center;
	}
	.operation{
		right: 225px;
    bottom: 83px;
		background: url(../../static/img/operation.png) no-repeat center;
	}
</style>



